import styled from 'styled-components';

const Container = styled('div')`
  width: 100vw;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  align-item: center;

  position: relative;
`;
const Title = styled('h1')`
  //   margin: 0;
  margin-bottom: 5px;
  color: #ffff;
  white-space: nowrap;
  font-family: 'Press Start 2P', cursive;
`;

const Link = styled('a')`
  font-size: 1.5rem;
  margin: 5rem;
  cursor: pointer;
  color: #ffff;
  text-decoration: none;
  font-family: 'Press Start 2P', cursive;

  position: absolute;
  right: 5%;

  @media (max-width: 700px) {
    position: initial;
  }
`;

const Navbar = () => {
  return (
    <Container>
      <Title>
        <div>
          <a href="https://www.normaneveryday.com/" target="_self">
            <img src="/NFTConceptmale.png" alt="" width={300} />
          </a>
        </div>
      </Title>

      {/* <Link href="https://www.normaneveryday.com/">Website</Link> */}
    </Container>
  );
};

export default Navbar;
